
section{
    padding: 2rem 9%;
    background-color: #101728;
}

/*header*/

header{
    position: fixed;
    top : 0; left :0; right :0;
    z-index: 1;
    background: var(--bg);
    box-shadow:  var(--box-shadow);
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 2.5rem 9%;
}


header .logo{
    font-size: 2.5rem;
    color: #fff;
    font-weight: bolder;
    
}

header .logo img{
    width:6%;
    padding-right: 1rem;
}


nav a{
    font-size: 1.7rem;
    color: white;
    margin-left: 2rem;
 }

 nav a:hover{
    color:var(--main-color);
 }

 header .nav-btn{
    padding: 5px; 
    cursor: pointer;
    background: transparent;
    border: none;
    outline: none;
    color: var(--main-color);
    visibility: hidden;
    opacity: 0;
    font-size: 1.8rem; 
 }


/* bouton*/

.btn{
    margin-top: 0.5rem;
    display: inline-block;
    padding: .8rem 3rem;
    font-size: 1.7rem;
    cursor: pointer;
    color:#fff;
    background: var(--degrade);
    border-radius: 2rem;
    box-shadow: var(--box-shadow);
    position: relative;
    z-index: 0;
    overflow: hidden;
}

.btn::before{
    content:'';
    position: absolute;
    top:0; right:0;
    height: 100%;
    width: 0%;
    background: var(--main-color);
    z-index: -1;
    transition: .2s ease-out;
}


.btn:hover:before{
    left:0;
    width:100%;
}

/*contenu home*/

.home{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 1.5rem;
    padding-top: 10rem;
    background: var(--bg);
    position: relative;
}

.home .content{
    flex: 1 1 45rem;
}

.home .image{
    flex: 1 1 45rem;
}

.home .image img{
    width: 100%;
}

.home .content h3{
    font-size: 5rem;
    color: white;
    line-height: 1.5;
    text-transform: none;
    padding-bottom: 3rem ;
} 

.home .cloud{
    position: absolute;
    bottom: 0; right: 0; left: 0;
    height: 18rem;
    background-size: 250rem 20rem;
    background: url(./image/cloud.png) repeat-x;
    animation: cloud 10s linear infinite;
}

@keyframes cloud{
    0%{
        background-position-x: 0rem;
    }
    0%{
        background-position-x: -250rem;
    }
}

.home .cloud-1{
    opacity: 0.7;
    height: 20rem;
    background-size: 250rem 18rem;
    animation-direction: reverse;
}

.second{
    text-align: center;
    color: white;
    text-transform: none; 
    padding-top: 14rem;
    padding-bottom: 14rem;
}
.second h3{
    font-size: 5rem;
    line-height: 1.8;
    color:var(--main-color);
    text-transform: none;
}

.second p{
    padding-top: 10rem;
    font-size: 2rem;
    line-height: 1.8;
}

.third{
    color: white;
    text-transform: none; 
    padding-top: 14rem;
    padding-bottom: 14rem;
    display: flex;
    flex-direction: row;
    text-align: center;
}

.images{
    flex: 50%;
}

.images img{
    border-radius: 3rem;
}

.text{
    flex: 50%;
}

.third h3{
    font-size: 5rem;
    line-height: 1.8;
    color:var(--main-color);
}

.third p{
    padding-top: 5rem;
    padding-bottom: 5rem;
    font-size: 2rem;
    line-height: 1.8;
}


.fourth{
    color: white;
    text-transform: none; 
    padding-top: 14rem;
    padding-bottom: 14rem;
    display: flex;
    flex-direction: row-reverse;
    text-align: center;
}

.fourth h3{
    font-size: 5rem;
    line-height: 1.8;
    color:var(--main-color);
}

.fourth p{
    padding-top: 5rem;
    padding-bottom: 5rem;
    font-size: 2rem;
    line-height: 1.8;
}

/*footer*/
.footer{

    margin-top: 0.1rem;
    padding-top: 10rem;
    background: var(--bg);
    text-align: center;

}

.footer .box-container{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(40rem, 1fr));
    gap: 1.5rem;
}

.footer .box-container .box{
    padding-top: 3rem;
}

.footer .box-container .box h3{
    font-size: 2.5rem;
    padding:1rem 0;
    color: var(--main-color);
    padding-bottom: 2rem;
}

.footer .box-container .box p{
    display: block;
    font-size: 1.5rem;
    padding:1rem 0;
    color: white;
}

.footer .box-container .box a{
    display: block;
    font-size: 1.5rem;
    padding:1rem 0;
    color: white;
}

.footer .box-container .box a i{
    padding-right: 3rem;
    color: white;
    
}

.footer .box-container .box a:hover i{
    padding-right: 2rem;
    color: var(--main-color);
}

.footer .log img{
    padding-top: 3rem;
    width:5%;
  
}

.footer .credit{
    color: white;
    padding: 1rem;
    margin-top: 2rem;
    padding-top: 5rem;
    font-size: 1.5rem;
    text-transform: none;
}

/* responsive*/


@media (max-width:1116px){

    html{
        font-size: 55%;
    }

    .header{
        padding: 2rem;
    }

    section{
        padding: 2rem;
    }
    
    .btn{
        margin-bottom: 5rem;
    }


}

@media (max-width:430px){

    html{
        font-size: 50%;
    }
    .images img{
        width: 300px;
        height: 300px;
    }
}  

@media only screen and (max-width: 1024px ){
    header .nav-btn{
        visibility: visible;
        opacity: 1;
    }

    header nav{
        position: fixed;
        top : 0; 
        left: 0; 
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column ;
        align-items: center;
        justify-content: center;
        gap: 1.5rem;
        background-color: var(--bg);
        transition: 1s;
        transform: translateY(-100vh);
    }

    header .responsive_nav{
         transform: none;
    }

    nav .nav-close-btn{
        position:absolute;
        top: 2rem;
        right: 2rem;
    }

    nav a{
        font-size: 1.5rem; 
    }

    .home .content{
        text-align: center;
    }

    .third{
        flex-direction: column;
        
    }

    .fourth{
        flex-direction: column;
    }


 }